import React, { Component } from 'react';
import { Table, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { Row, Col, Button } from 'react-bootstrap';

export default class table extends Component {
    constructor(props) {
        super(props)
        this.state = {
            selectedColumn: '',
            selectedColumnUniqueSet: [],
            clickableColumns: ['Area', 'Zone', 'Bank', 'Stand', 'Asset', 'Date'],
            tableHeight: ''
        }
        this.resetFilter = this.resetFilter.bind(this);
    }

    updateSelectedColumn(column) {
        this.setState({ selectedColumn: column });
        var columnIndex = this.props.data[0].indexOf(column)
        var uniqueSetArray = [];
        this.props.data.map((rowData, index) => {
            if (index != 0 && rowData[0].length != 0) {
                var arrayIndex = this.getArrayObjectIndex(uniqueSetArray, rowData[columnIndex]);
                if (arrayIndex == -1) {
                    var selectedColumnObject = new Object();
                    selectedColumnObject.selectedColumn = rowData[columnIndex];
                    selectedColumnObject.columns = [];
                    selectedColumnObject.columns.push(rowData)
                    uniqueSetArray.push(selectedColumnObject)
                } else {
                    uniqueSetArray[arrayIndex].columns = [...uniqueSetArray[arrayIndex].columns, rowData]
                }
            }
        });
        this.setState({
            selectedColumnUniqueSet: uniqueSetArray
        })
    }

    convertStringToNumber(value) {
        var returnValue;
        Number(value) != NaN ? returnValue = Number(value) : returnValue = value
        return returnValue;
    }

    getArrayObjectIndex(graphArray, selectedColumn) {
        return graphArray.findIndex(x => x.selectedColumn === selectedColumn);
    }
    resetFilter() {
        this.setState({
            selectedColumn: '',
            selectedColumnUniqueSet: []
        })
    }
    componentDidMount() {
        var lineChartHeight = document.getElementById('lineChartDiv').offsetHeight;
        this.setState({
            tableHeight: (window.innerHeight - lineChartHeight - 55 - 45 - 16) //55-margin-top; 45-height of table headring; 16 -margin top and bottom for each divs , 4 each
        })
    }
    render() {
        return (
            <React.Fragment>
                <Row className="m-1 tableHeading">
                    <Col xs={{ span: 8, offset: 2 }}>
                        <h4 className="text-center mb-0">Data Table</h4>
                    </Col>
                    <Col xs={2}>
                        <Button variant="primary" size="sm" block disabled={this.state.selectedColumn.length == 0 ? 'disabled' : ''} onClick={this.resetFilter}>Reset Filter</Button>
                    </Col>
                </Row>

                <Row className="m-1">
                    <Col xs={12} className="mt-1">
                        <div className="table-responsive" style={{ height: this.state.tableHeight }}>
                            <Table className="dataTable">
                                <thead>
                                    <tr>
                                        {
                                            this.props.data.length == 0
                                                ? ''
                                                : this.props.data[0].map(data => {
                                                    if (this.state.selectedColumn == data) {
                                                        return <td className="tableHeader selectedColumnHeader" onClick={() => this.updateSelectedColumn(data)}>{data}</td>
                                                    } else {
                                                        return <td className="tableHeader" onClick={() => this.state.clickableColumns.indexOf(data) != -1 ? this.updateSelectedColumn(data) : ''}>{data}</td>
                                                    }
                                                })
                                        }
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        this.props.data.length == 0
                                            ? ''
                                            : this.state.selectedColumnUniqueSet.length == 0
                                                ? this.props.data.map((rowData, index) => {
                                                    if (index != 0 && rowData[0].length != 0) {
                                                        return (
                                                            <tr>{rowData.map(data => {
                                                                return <td>{data}</td>
                                                            })
                                                            }</tr>
                                                        )
                                                    }
                                                })
                                                : this.state.selectedColumnUniqueSet.map(selectedColumn => {
                                                    var columnIndex = this.props.data[0].indexOf(this.state.selectedColumn);
                                                    return (
                                                        selectedColumn.columns.map((columns, rowIndex) => {
                                                            return (

                                                                <tr className={selectedColumn.columns.length == (rowIndex + 1) ? 'endOfArrayBorder' : ''}>
                                                                    {
                                                                        columns.map((data, index) => {
                                                                            if (rowIndex != 0 && index == columnIndex) {

                                                                            } else {
                                                                                if (index == columnIndex) {
                                                                                    return <td className="tdRowSpan" rowSpan={selectedColumn.columns.length}>{data}</td>
                                                                                } else {
                                                                                    return <OverlayTrigger key={rowIndex + " " + index} placement={'bottom'} overlay={<Tooltip id={index}>Group Filter : ({this.state.selectedColumn} {columns[columnIndex]})</Tooltip>}><td>{data}</td></OverlayTrigger>
                                                                                }

                                                                            }
                                                                        })
                                                                    }
                                                                </tr>

                                                            )
                                                        }))
                                                })

                                    }
                                </tbody>
                            </Table >
                        </div>
                    </Col>
                </Row>
            </React.Fragment>
        )
    }
}
