import React, { Component } from 'react'
import { Line, Bar } from 'react-chartjs-2'
import { Row, Col } from 'react-bootstrap';

const options = { legend: { display: false }, scales: { yAxes: [{ ticks: { beginAtZero: true } }] } }

export default class chart extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: {
                labels: [],
                datasets: [{
                    label: 'NetWin',
                    fill: false,
                    lineTension: 0.1,
                    backgroundColor: '#36A2EB',
                    borderColor: '#36A2EB',
                    borderCapStyle: 'butt',
                    borderDash: [],
                    borderDashOffset: 0.0,
                    borderJoinStyle: 'miter',
                    pointBorderColor: '#36A2EB',
                    pointBackgroundColor: '#36A2EB',
                    pointBorderWidth: 7,
                    pointHoverRadius: 5,
                    pointHoverBackgroundColor: '#FF6384',
                    pointHoverBorderColor: '#FF6384',
                    pointHoverBorderWidth: 1,
                    pointRadius: 1,
                    pointHitRadius: 10,
                    data: []
                }]

            }
        }
        this.getArrayObjectIndex = this.getArrayObjectIndex.bind(this)
    }

    componentWillReceiveProps(nextProps, nextState) {
        var dateIndex = nextProps.data[0].indexOf('Date');
        var netWinIndex = nextProps.data[0].indexOf('NetWin');
        if (this.props.data != nextProps.data) {
            var graphArray = [];
            nextProps.data.map((data, index) => {
                if (index != 0 && data[0].length != 0) {
                    var getIndex = this.getArrayObjectIndex(graphArray, data[dateIndex]);
                    if (getIndex == -1) {
                        var graphObject = new Object();
                        graphObject.date = data[dateIndex];
                        graphObject.netwin = data[netWinIndex];
                        graphArray.push(graphObject)
                    } else {
                        graphArray[getIndex].netwin = Number(graphArray[getIndex].netwin) + Number(data[netWinIndex])
                    }
                }
                var dateArray = [];
                var netWinArray = [];
                graphArray.map(dataValue => {
                    dateArray.push(dataValue.date);
                    netWinArray.push(dataValue.netwin);
                })
                this.setState(prevState => ({
                    data: {
                        ...prevState.data,
                        labels: dateArray,
                        datasets: [Object.assign({}, this.state.data.datasets[0], { data: netWinArray })]
                    }
                }))

            });

        }
    }
    getArrayObjectIndex(graphArray, date) {
        return graphArray.findIndex(x => x.date === date);
    }
    
    render() {
        return (
            <Row className="m-1" id="lineChartDiv">
                <Col>
                    <h4 className="text-center">NetWin vs Date</h4>
                    <Line height={50} ref={this.chartReference} data={this.state.data} options={options} />
                </Col>
            </Row>
        )
    }
}
